@charset "UTF-8";
.QMTable-Button-danger {
	display: inline-block;
	text-decoration: none;
	font-size: 13px;
	line-height: 26px;
	height: 28px;
	margin: 0;
	padding: 0 10px 1px;
	cursor: pointer;
	border-width: 1px;
	border-style: solid;
	-webkit-appearance: none;
	border-radius: 3px;
	white-space: nowrap;
	box-sizing: border-box;
}

.QMTable-Button-danger {
	color: #fff;
	background-color: #dc3545;
	border-color: #dc3545;
}

.QMTable-Button-danger:hover, .QMTable-Button-danger:focus {
	color: #fff;
	background-color: #c82333;
	border-color: #bd2130;
}

.QMTable-Button-danger:focus {
	box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, 0.5);
}

.QMErrorColumn-Modal {
	font-size: 13px;
}

.QMErrorColumn-Modal p {
	font-size: inherit;
}

.QMErrorColumn-Modal pre {
	white-space: pre-wrap;
	font-size: 12px;
}

.QMErrorColumn-level0:before, .QMErrorColumn-level1:before, .QMErrorColumn-level2:before {
	display: inline-block;
	margin-right: 3px;
	font: normal normal normal 14px/1 FontAwesome;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.QMErrorColumn-level0:before {
	color: #17a2b8;
	content: '\f05a';
}

.QMErrorColumn-level1:before {
	color: #ffc107;
	content: '\f06a';
}

.QMErrorColumn-level2:before {
	color: #dc3545;
	content: '\f057';
}

.QMErrorColumn-Message {
	cursor: pointer;
}

.QMErrorColumn-Message > b:after {
	content: '▼';
	margin-left: 5px;
}

.QMErrorColumn-Details {
	display: none;
}

.QMErrorColumn-active .QMErrorColumn-Details {
	display: block;
}

.QMErrorColumn-active > b:after {
	content: '▲';
}

.QMTable {
	display: block;
	width: 100%;
	overflow-x: auto;
	-webkit-overflow-scrolling: touch;
	-ms-overflow-style: -ms-autohiding-scrollbar;
}

.QMTable-Button {
	margin-top: 1px !important;
	margin-bottom: 1px !important;
}

.QMTable-Button:hover {
	vertical-align: baseline !important;
}

.QMTable-List {
	margin: 0 0 0 1rem;
}

.QMTable-List li {
	list-style-type: disc;
}

.QMTable-Search, .QMTable-SearchIn {
	height: 28px;
	margin: 1px;
	padding: 3px 5px;
	vertical-align: bottom;
}

.QMTable-SearchBox .select2 {
	height: 29px;
}

.QMTable-SearchBox .select2-selection, .QMTable-SearchBox .select2-dropdown {
	border-color: #ddd;
}

.QMTable > table {
	width: 100%;
	max-width: 100%;
}

.QMTable-focusedRow {
	box-shadow: inset 0 0 2px rgba(51, 51, 51, 0.6);
}

.QMProgress {
	margin-bottom: 15px;
}

.QMProgress-Bar {
	display: flex;
	height: 1.2rem;
	overflow: hidden;
	font-size: 0.75rem;
	background-color: #ffffff;
	border-radius: 0.25rem;
	border: 1px solid #e1e1e1;
}

.QMProgress-Info {
	position: absolute;
	width: 100%;
	text-align: center;
	line-height: 1.2rem;
	text-shadow: 1px 1px 1px #fff, 1px -1px 1px #fff, -1px 1px 1px #fff, -1px -1px 1px #fff;
}

.QMProgress-Progress {
	display: flex;
	flex-direction: column;
	justify-content: center;
	color: #ffffff;
	text-align: center;
	white-space: nowrap;
	background-color: #0085ba;
	transition: width 0.25s cubic-bezier(0.4, 0, 0.2, 1);
}

.QMProgress-Progress-striped {
	background-image: linear-gradient(45deg, rgba(255, 255, 255, 0.15) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.15) 50%, rgba(255, 255, 255, 0.15) 75%, transparent 75%, transparent);
	background-size: 1rem 1rem;
}

.QMProgress-Progress-animated {
	animation: QMProgress-Progress-animation 1s linear infinite;
}

@keyframes QMProgress-Progress-animation {
	from {
		background-position: 0 0;
	}
	to {
		background-position: 1rem 0;
	}
}

